import React from "react";
import { Redirect } from 'react-router-dom';
import axios from "axios";
import {VALIDATE_PROJECT_ID, VALIDATE_LOGGED_IN_SESSION} from "../api";
import LoadingIndicator from "../loadingIndicator";

class Validation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      uniqueId: props.match.params.projectId,
      destination: props.match.params.destination,
      loginId: props.match.params.loginId,
      redirectTo: ''
    };

    var self = this;

    this.validate_user_login_id(self);

  }

  validate_user_login_id(self){
    axios.post(VALIDATE_LOGGED_IN_SESSION, {
    uniqueId:self.state.loginId
    })
    .then(function (response) {
      console.log(response);
       if(response.status===200){
         if(response.data==="Valid"){
           console.log(self.state);
           if(self.state.uniqueId==="::"){
             localStorage.setItem("clear","ok");
             self.setState({
               redirectTo: <Redirect to={{
                pathname: '/stark/'+localStorage.getItem("redirectTo"),
                state: { validated: true }
            }}/>
             });
           }
           else{
             self.validate_project_id(self);
           }
         }
         else if(response.data==="Invalid" || response.data==="Credentials not received"){
           localStorage.removeItem("clear");
           localStorage.removeItem("starkLoginId");
           localStorage.removeItem("redirectTo");
           self.setState({
             redirectTo: <Redirect to={{
              pathname: '/stark/login',
              state: { validated: false }
          }}/>
           });
         }
       }
     })
     .catch(function (error) {
       console.log(error);
       localStorage.removeItem("clear");
       localStorage.removeItem("starkLoginId");
       localStorage.removeItem("redirectTo");
       return window.location.href = '/stark/login';
     });
  }

  validate_project_id(self){
    axios.post(VALIDATE_PROJECT_ID, {
    projectId:self.state.uniqueId,
    userId: self.state.loginId
    })
    .then(function (response) {
      console.log(response);
       if(response.data==="Valid"){
         localStorage.setItem("clear","ok");
         self.setState({
           redirectTo: <Redirect to={{
            pathname: '/stark/'+self.state.destination+'/'+self.state.uniqueId,
            state: { validated: true }
        }}/>

         });
       }
       else{
         console.log("4041");
         localStorage.removeItem("clear");
         return window.location.href = 'https://champsinc.github.io/404';
       }
     })
     .catch(function (error) {
       console.log("4042");
       localStorage.removeItem("clear");
       return window.location.href = 'https://champsinc.github.io/404';
     });
  }

  render(){
    return (
      <div>
      <LoadingIndicator styleProp={"block"}/>
      {this.state.redirectTo}
      </div>
    );
  }
}
export default Validation;
